import Card from '@mui/material/Card';
import CardMedia from '@mui/material/CardMedia';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Grid from '@mui/material/Grid';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import SubHeader from './SubHeader';
import Box from '@mui/material/Box';

function NameCard(){
    return(
        <Card variant="outlined" sx={{ height: '100%', borderRadius: 4}}>
            <CardContent>
                <Typography  component="h3">
                     
                </Typography>
                    <Typography color="textSecondary" component="h3">
                    ( )
                </Typography>
                <Typography>
                    
                </Typography>
            </CardContent>
        </Card>)
}

function ComeFromCard(){
    return(
        <Card variant="outlined" sx={{ height: '100%',borderRadius: 4}}>
            <CardContent>
                <Typography  component="h3">
                    
                </Typography>
            </CardContent>
        </Card>)
}

function MailAddressCard(){
    return(
        <Card variant="outlined" sx={{ height: '100%',borderRadius: 4}}>
            <CardContent>
                <Typography  component="h3">
                    MailAddress:
                </Typography>
                <Typography  component="h4">
                    tanimoto0420.job@gmail.com
                </Typography>
                <br />
                <Typography  component="h3">
                    SubAddress:
                </Typography>
                <Typography  component="h4">
                    s2520086@wakayama-u.ac.jp
                </Typography>
            </CardContent>
        </Card>)
}

function AffiliationCard(){
    return(
        <Card variant="outlined" sx={{ height: '100%',borderRadius: 4}}>
            <CardContent>
                <Typography  component="h3">
                    
                </Typography>
                <Typography  component="h3">
                    
                </Typography>
                /n
                <Typography  component="h3">
                    
                </Typography>
                <Typography  component="h3">
                    
                </Typography>
            </CardContent>
        </Card>)
}

function OSCPicCard(){
    return(
        <Card variant="outlined" sx={{ height: "100%",borderRadius: 4}}>
            <CardMedia
                component="img" // img
                height="100%"    // 
                image={"/images/OSS.jpeg"} // URL
                alt="OSCpic" // 
                sx={{ objectFit: 'fill' }}
            />
        </Card>)
}

function TravelPicCard(){
    return(
        <Card variant="outlined" sx={{ height: "100%",borderRadius: 4}}>
            <CardMedia
                component="img" // img
                image={"/images/travel.jpeg"} // URL
                height="100%"    // 
                alt="Travel" // 
                sx={{ objectFit: 'comfort' }}
            />
        </Card>)
}

function Introduce(){
    return(
        <Box sx={{height:"100%"}}>
        {SubHeader("")}
        <Stack sx={{ height: '85%' }} spacing={1}>
            <Grid container spacing={1} justifyContent="center" sx={{ height: '50%' }}>
                <Grid size={4} sx={{ height: '100%' }}>
                    {NameCard()}
                </Grid>
                <Grid size={4} sx={{ height: '100%' }}>
                    {ComeFromCard()}
                </Grid>
                <Grid size={4} sx={{ height: '100%' }}>
                    {MailAddressCard()}
                </Grid>
            </Grid>
            <Grid container spacing={1} justifyContent="center" sx={{ height: '50%' }}>
                <Grid size={4} sx={{ height: '100%' }}>
                    {AffiliationCard()}
                </Grid>
                <Grid size={4} sx={{ height: '100%' }}>
                    {OSCPicCard()}
                </Grid>
                <Grid size={4} sx={{ height: '100%' }}>
                    {TravelPicCard()}
                </Grid>
            </Grid>
        </Stack>
        </Box>
    )
}

export default Introduce